<template>
    <div class="goodslist">
        <yd-infinitescroll :callback="loadMore" :distance="2" :scroll-top="false" ref="infinitescrollDemo">
            <yd-list theme="4" slot="list">
                <yd-list-item v-for='(item, key) in list' :key='key' type="link" :href="{path:'goodsdetail',query:{goods_id:item.id}}">
                    <img slot='img' :src='item.image_url'>
                    <span slot='title'>{{item.name}}</span>
                    <yd-list-other slot='other'>
                        <div>
                            <span class='demo-list-price'><em></em>{{item.price}}</span>
                        </div>
                    </yd-list-other>
                </yd-list-item>
            </yd-list>

            <!--  -->
            <span slot="doneTip" class="data-none"></span>

            <!--  -->
            <img slot="loadingTip" src="http://static.ydcss.com/uploads/ydui/loading/loading10.svg"/>
        </yd-infinitescroll>
        <yd-backtop></yd-backtop>
    </div>
</template>

<script>
export default {
    data () {
        return {
            page: 1,
            pageSize: 10, // 10
            catId: this.$route.query.cat_id || 0, // 
            hot: this.$route.query.hot || 0, // 
            keywords: this.$route.query.keywords || 0, // 
            list: [] // 
        }
    },
    mounted () {
        this.goodsList()
    },
    methods: {
        // 
        goodsList () {
            this.$api.goodsList({
                page: this.page,
                limit: this.pageSize,
                where: this.conditions()
            }, res => {
                const _list = res.data.list
                this.list = [..._list]
                if (_list.length < this.pageSize) {
                    /*  */
                    this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone')
                }
            })
        },
        // 
        loadMore () {
            this.$api.goodsList({
                page: ++this.page,
                limit: this.pageSize,
                where: this.conditions()
            }, res => {
                const _list = res.data.list
                this.list = [...this.list, ..._list]
                if (_list.length < this.pageSize) {
                    /*  */
                    this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone')
                }
                /*  */
                this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad')
            })
        },
        // 
        conditions () {
            let where = {}
            if (this.catId) {
                where = {cat_id: this.catId}
            } else if (this.hot) {
                where = {hot: this.hot}
            } else if (this.keywords) {
                where = {search_name: this.keywords}
            }
            return JSON.stringify(where)
        }
    }
}
</script>

<style>
</style>



