function getConfig () {
    return {
        apiUrl: '/index.php/api.html',
        siteToken: '11111111111111111111111111111111'
    }
}

function post (method, data, callback, show = true) {
    // token
    if (methodToken.indexOf(method) >= 0) {
        data.token = window.localStorage.getItem('user_token')
    }
    data.method = method
    data.site_token = getConfig().siteToken // common.getStorage('siteToken') // site_token
    let url = getConfig().apiUrl // common.getStorage('url') // api_url
    axios.post(url, qs.stringify(data)).then(response => {
        callback(response.data)
        //  token
        if (response.data.data === 14007 || response.data.data === 14006) {
            // user_token
            common.removeStorage('user_token')
            // 
            common.errorToBack(response.data.msg)
            // 
            common.jumpToLogin()
        }
    }).catch(error => {
        console.log(error)
    })
}

// 
function uploadImages (param, callback) {
    let config = {
        headers: {'Content-Type': 'multipart/form-data'}
    }
    param.append('method', 'images.upload')
    let siteToken = getConfig().siteToken // common.getStorage('siteToken') // site_token
    let url = getConfig().apiUrl // common.getStorage('url') // api_url
    param.append('site_token', siteToken)
    axios.post(url, param, config).then(response => {
        callback(response.data)
    }).catch(error => {
        console.log(error)
    })
}

